<template>
  <div class='root'>
    <action-bar title='创建预售活动'></action-bar>
    <div class='container'>
      <scroll-view class='content' :data="goodsList">
        <div class="cc">
          <div class="content-box">
            <div class="flex-center margin-bottom">
              <span>标题：</span>
              <input v-model="title" class="title" type="text" maxlength="10" placeholder="请输入活动标题10个字以内" />
            </div>
            <div class="flex-center margin-bottom">
              <span>类型：</span>
              <van-radio-group checked-color="#42D18A" v-model="radio">
                <van-radio name="固定模式">固定模式</van-radio>
              </van-radio-group>
            </div>
            <div class="margin-bottom">
              <div class="margin-bottom">活动时间：</div>
              <div class="flex-center">
                <div @click="bindStartSales" class="date">
                  <input :value="startSalesTime" class="input-date" type="text" disabled placeholder="开始时间">
                </div>
                <span class="to">至</span>
                <div @click="bindEndSales" class="date">
                  <input :value="endSalesTime" class="input-date" type="text" disabled placeholder="结束时间">
                </div>
              </div>
            </div>
            <div class="margin-bottom">
              <div class="margin-bottom">预计送达时间段：</div>
              <div class="flex-center">
                <div @click="bindSdStart" class="date">
                  <input :value="startDeliveryDate" class="input-date" type="text" disabled placeholder="开始时间">
                </div>
                <span class="to">至</span>
                <div @click="bindSdEnd" class="date">
                  <input :value="endDeliveryDate" class="input-date" type="text" disabled placeholder="结束时间">
                </div>
              </div>
            </div>
          </div>
          <div class="content-box">
            <div class="flex-center goods-add margin-bottom">
              <div class="goods-title">添加商品</div>
              <div class="goods-quantity">共{{ goodsList.length }}件</div>
            </div>
            <div class="add-goods-box">
              <div class="no-font-size" @click="bindAddGoods">
                <img class="add-goods-img" src="@/assets/home_add_commoidy_icon.png" />
              </div>
              <div class="hor-goods-box">
                <div class="hor-scroll">
                  <van-image v-for="item in goodsList" :key="item.id" class="hor-goods-img" :src="baseUrl + item.c" />
                </div>
              </div>
            </div>
          </div>
          <goods-item v-for="(item, index) in goodsList" :key="item.id" class="content-box">
            <van-image slot="imgSlot" radius=".25rem" :src="baseUrl + item.imageUrl" class="goods-img" />
            <div slot="nameSlot">
              <img class="delete-img" src="@/assets/icon_chongzhi.png" @click="bindDeleteGoods(index)" />
              <div>{{ item.productName }}</div>
            </div>
            <div slot="attributeSlot">
              <div class="attribute">
                <div class="attribute-content">分类：{{ item.categoryName }}</div>
                <div class="attribute-content">颜色：{{ item.color }}</div>
              </div>
              <div class="attribute">
                <div class="attribute-content">等级：{{ item.level }}</div>
                <div class="attribute-content">规格：{{ item.unit }}</div>
              </div>
            </div>
            <div slot="menuSlot" class="goods-bottom">
              <div class="price-stock">
                <span>价格(元)</span><input maxlength="10" type="digit" placeholder="0.00元" v-model="item.priceStay"
                  class="input-bg" />
              </div>
              <div class="price-stock">
                <span>库存(扎)</span><input type="number" maxlength="10" placeholder="0扎" v-model="item.stockStay"
                  class="input-bg" />
              </div>
            </div>
          </goods-item>
        </div>
      </scroll-view>
    </div>
    <div class="bottom">
      <div class="agree">
        <van-checkbox class="ck" icon-size="16px" v-model="isPrivacy" checked-color="#42D18A"></van-checkbox>
        <div>查看并同意<a :href="baseUrl + '/static/notice/reserve_shop.html'" target="_blank" class="privacy">《预售协议》</a>
        </div>
      </div>

      <div @click="bindSubmit" :class="['release', isPrivacy ? 'release-true' : 'release-false']">发布</div>

    </div>
    <date-picker-popup ref="hdks" @confirm="hdks"></date-picker-popup>
    <date-picker-popup ref="hdjs" @confirm="hdjs"></date-picker-popup>
    <date-picker-popup ref="sdks" @confirm="sdks"></date-picker-popup>
    <date-picker-popup ref="sdjs" @confirm="sdjs"></date-picker-popup>
  </div>
</template>
<script>

import { Checkbox, RadioGroup, Radio } from 'vant'
import DatePickerPopup from '@/components/util/DatePickerPopup.vue'
import goodsItem from '@/components/util/GoodsItem.vue'
import { addPresellActivity } from '@/http/api'

export default {
  name: 'addReserveShop',
  components: {
    DatePickerPopup,
    [Checkbox.name]: Checkbox,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    goodsItem
  },
  computed: {
    baseUrl () {
      return this.$store.state.baseUrl
    }
  },
  data () {
    return {
      radio: '固定模式',
      isMode: true,
      isPrivacy: false,
      title: '', // 标题
      goodsList: [], // 商品信息
      priceModel: '固定模式', // 类型
      startTimeLimit: '', // 活动开始时间限制
      startSalesTime: '', // 活动开始时间
      endSalesTime: '', // 活动结束时间
      startDeliveryDate: '', // 起送开始时间
      endDeliveryDate: '', // 起送结束时间
      startDeliveryTimeLimit: '' // 起送开始时间限制
    }
  },
  methods: {
    bindSubmit () {
      if (!this.isPrivacy) {
        this.$toast('请阅读并同意预售协议')
        return
      }
      const title = this.title
      if (!title) {
        this.$toast('请输入活动标题')
        return
      }
      const startSalesTime = this.startSalesTime
      if (!startSalesTime) {
        this.$toast('请选择活动开始时间')
        return
      }
      const endSalesTime = this.endSalesTime
      if (!endSalesTime) {
        this.$toast('请选择活动结束时间')
        return
      }
      const startDeliveryDate = this.startDeliveryDate
      if (!startDeliveryDate) {
        this.$toast('请选择起送开始时间')
        return
      }
      const endDeliveryDate = this.endDeliveryDate
      if (!endDeliveryDate) {
        this.$toast('请选择起送结束时间')
        return
      }
      const goodsList = this.goodsList
      if (goodsList.length === 0) {
        this.$toast('请添加预售商品')
        return
      }
      const item = []
      for (const goods of goodsList) {
        if (!goods.priceStay) {
          this.$toast('<' + goods.productName + '>价格未填写')
          return
        }
        if (!goods.stockStay) {
          this.$toast('<' + goods.productName + '>库存未填写')
          return
        }
        item.push({
          price: goods.priceStay,
          inventory: goods.stockStay,
          productId: goods.productId
        })
      }
      const obj = {
        title,
        priceModel: this.priceModel,
        startSalesTime,
        endSalesTime,
        startDeliveryDate,
        endDeliveryDate,
        items: item
      }
      const data = {
        data: JSON.stringify(obj)
      }
      addPresellActivity(data).then(res => {
        this.$router.back()
        this.$router.push('/reserveShopList')
        this.$toast.success('创建成功')
      }).catch(res => {
        this.$toast(res.msg)
      })
    },
    bindDeleteGoods (index) {
      const goodsList = this.goodsList
      if (index < goodsList.length) {
        goodsList.splice(index, 1)
        this.goodsList = goodsList
      }
    },
    bindEndSales () {
      if (!this.startSalesTime) {
        this.$toast('请选择活动开始时间')
        return
      }
      this.$refs.hdjs.showPopup()
    },
    bindStartSales () {
      this.$refs.hdks.showPopup()
    },
    bindSdStart () {
      if (!this.startSalesTime) {
        this.$toast('请选择活动开始时间')
        return
      }
      if (!this.endSalesTime) {
        this.$toast('请选择活动结束时间')
        return
      }
      this.$refs.sdks.showPopup()
    },
    bindSdEnd () {
      if (!this.startDeliveryDate) {
        this.$toast('请选择送达开始时间')
        return
      }
      this.$refs.sdjs.showPopup()
    },
    hdks (date) {
      this.startSalesTime = date
    },
    hdjs (date) {
      this.endSalesTime = date
    },
    sdks (date) {
      this.startDeliveryDate = date
    },
    sdjs (date) {
      this.endDeliveryDate = date
    },
    bindAddGoods () {
      this.$router.push('/spotGoodsList')
    }
  },
  mounted () {
  },
  beforeRouteEnter (to, from, next) {
    next()
    const list = from.params.list
    if (list) {
      next(vm => {
        vm.goodsList = list
      })
    }
  }
}
</script>
